.top-button {
  height: 0px; }

.menu-button {
  display: none;
  cursor: pointer;
  background-color: #922d4a;
  float: right;
  margin: 7px 7px;
  height: 40px;
  z-index: 4000;
  box-shadow: 1px 1px 8px 1px rgba(32, 64, 92, 0.41); }

.bar1,
.bar2,
.bar3 {
  width: 25px;
  height: 1px;
  margin: 5.5px 5.5px;
  border: 2px solid #ffffff !important;
  transition: 0.4s;
  border-radius: 200px 200px 200px 200px;
  -moz-border-radius: 200px 200px 200px 200px;
  -webkit-border-radius: 200px 200px 200px 200px;
  box-shadow: 10px 9px 21px -11px rgba(0, 0, 0, 0.75);
  background: white; }

.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px); }

.change .bar2 {
  opacity: 0; }

.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px); }

.nav-appear {
  display: block !important;
  -webkit-animation: appear 0.5s normal 1;
  animation: appear 0.5s normal 1; }

@-webkit-keyframes appear {
  from {
    opacity: 0.4;
    -webkit-transform: translateX(25px);
    transform: translateX(25px); }
  to {
    opacity: 0.95;
    -webkit-transform: translateX(0px);
    transform: translateX(0px); } }

@keyframes appear {
  from {
    opacity: 0.4;
    -webkit-transform: translateX(25px);
    transform: translateX(25px); }
  to {
    opacity: 0.95;
    -webkit-transform: translateX(0px);
    transform: translateX(0px); } }

.arrow-turn-down {
  -webkit-animation: turn-down-arrow 0.5s;
  animation: turn-down-arrow 0.5s;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

@-webkit-keyframes turn-down-arrow {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(-315deg);
    transform: rotate(-315deg); } }

@keyframes turn-down-arrow {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(-315deg);
    transform: rotate(-315deg); } }

.dropdown-block-appear-transition {
  -webkit-animation: dropdown-block-appear 0.5s normal 1;
  animation: dropdown-block-appear 0.5s normal 1; }

@-webkit-keyframes dropdown-block-appear {
  from {
    opacity: 0.4;
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px); }
  to {
    opacity: 0.95;
    -webkit-transform: translateY(0px);
    transform: translateY(0px); } }

@keyframes dropdown-block-appear {
  from {
    opacity: 0.4;
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px); }
  to {
    opacity: 0.95;
    -webkit-transform: translateY(0px);
    transform: translateY(0px); } }

@media screen and (min-width: 1100px) {
